<template>
  <b-container fluid>
    <b-row class="mt-1" v-for="(line, index) in lines" v-bind:key="index">
      <b-form inline>
        <b-form-input @change="updateLines" style="width: 210px" v-model="line.number"
                numeric-keyboard-toggle
                placeholder="Número telefonico..."
                type="text"
                value="">
        </b-form-input>

        <b-form-select class="ml-1" @change="updateLines"
          v-model="line.type"
          :options="types">
          <template slot="first">
            <option :value="null" disabled>- Tipo -</option>
          </template>
        </b-form-select>

        <b-button class="px-2 ml-1" v-if="index + 1 === lines.length" @click="addLine" variant="success" size="sm"><i class="far fa-copy"></i></b-button>
        <b-button class="px-2 ml-1" :disabled="index == 0" @click="removeLine(index)" variant="danger" size="sm"><i class="far fa-trash-alt"></i></b-button>
      </b-form>
    </b-row>
  </b-container>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'PhoneNumberLine',
  computed: {
    ...mapGetters({
      lines: 'contacts/getLinesNumbers'
    })
  },
  data () {
    return {
      // lines: [],
      blockRemoval: true,
      types: [
        { text: 'Movil', value: '0' },
        { text: 'Trabajo', value: '1' },
        { text: 'Casa', value: '2' },
        { text: 'Fax', value: '3' }
      ]
    }
  },
  watch: {
    lines () {
      if (this.lines.length < 1) {
        this.blockRemoval = false
        this.addLine()
      } else {
        this.blockRemoval = true
      }
    }
  },
  methods: {
    updateLines () {
      this.$store.commit('contacts/setLinesNumbers', this.lines)
    },
    addLine () {
      this.lines.push({
        type: 0,
        number: '',
        id_contact: 0,
        id: 0
      })
    },
    removeLine (lineId) {
      if (this.blockRemoval) {
        this.lines.splice(lineId, 1)
      }
    }
  },
  mounted () {
    if (this.lines.length === 0) {
      this.addLine()
    }
  }
}
</script>



