import commonManager from '../../services/commonManager.Service'
import store from '../../vuex/index'

export default {
  namespaced: true,
  state: {
    groupId: 0,
    groupItem: [],
    members: []
  },

  getters: {
    getGroupById: (state) => {
      return state.groupId
    }
  },

  mutations: {
    setGroupId (state, id) {
      state.groupId = id
      state.groupItem = []
    },

    setGroupItem (state, data) {
      state.groupItem = data
    }
  },

  actions: {
    getListGroup (context, params) {
      return new Promise((resolve, reject) => {
        var url = `/workgroup/${store.state.workgroupId}/group/page/${params.currentPage}`
        var sortField = ''
        var filterField = ''

        if (params.sortBy) {
          sortField = params.sortDesc ? `?orderbydesc=${params.sortBy}` : `?orderby=${params.sortBy}`
        } else {
          sortField = `?orderby=name`
        }

        if (params.filter) {
          filterField = `&name=['like', '%${params.filter}%']&description=['or', 'like', '%${params.filter}%']`
        }

        url = url + sortField + filterField

        commonManager.getListRecord(url).then((response) => {
          resolve(response)
        }).catch(response => {
          reject(response)
        })
      })
    },

    getListMembers (context, params) {
      return new Promise((resolve, reject) => {
        var url = `/workgroup/${store.state.workgroupId}/group/${context.state.groupId}/member/page/${params.currentPage}`
        // var sortField = ''
        var filterField = ''

        /*
        if (params.sortBy) {
          sortField = params.sortDesc ? `?orderbydesc=${params.sortBy}` : `?orderby=${params.sortBy}`
        } else {
          sortField = `?orderby=name`
        }
        */

        if (params.filter) {
          filterField = `&name=['like', '%${params.filter}%']&email=['or', 'like', '%${params.filter}%']`
        }

        // por motivos de error el backend el campo sortField no puede ser utilizado CORREGIR
        url = url + filterField

        commonManager.getListRecord(url).then((response) => {
          resolve(response)
        }).catch(response => {
          reject(response)
        })
      })
    },

    setGroupId (context, id) {
      context.commit('setGroupId', id)
      return new Promise((resolve, reject) => {
        context.dispatch('getGroupById').then((response) => {
          resolve(response)
        }).catch(response => {
          reject(response)
        })
      })
    },

    getGroupById (context) {
      return new Promise((resolve, reject) => {
        var url = `/workgroup/${store.state.workgroupId}/group/${context.state.groupId}`
        commonManager.getRecordById(url).then((response) => {
          context.commit('setGroupItem', response.data)
          resolve(response.data)
        }).catch(response => {
          reject(response)
        })
      })
    },

    deleteGroup (context, record) {
      return new Promise((resolve, reject) => {
        var url = `/workgroup/${store.state.workgroupId}/group/`
        commonManager.deleteRecord(url, record)
          .then((response) => {
            resolve(response)
          }).catch(response => {
            console.error('Fallo eliminar grupo, ERROR: ' + response.body.message)
            reject(response)
          })
      })
    }
  }
}



