<template>
  <b-container fluid>
    <b-row align-v="center" class="ml-1 mt-4 mb-4">
      <h4 class="text-success">Estadisticas</h4>
      <div class="v-divider"></div>
      <b-col>
        <router-link :to="{name: 'dashboard'}"><i class="fas fa-home text-muted mr-2 link--top"></i></router-link>-
        <a class="ml-2">Estadisticas</a>
      </b-col>
    </b-row>

    <b-row align-h="end">
      <b-col cols="2">
        <b-select v-model="selected" :options="options" @change="onChange(selected)"/>
      </b-col>
    </b-row>
    <b-row align-h="center" class="mt-3 mb-5">
      <b-col cols="5" class="mr-5" align="center">
        <line-chart ytitle="Mensajes" :data="chartData"></line-chart>
      </b-col>
      <b-col cols="5" align="center">
        <column-chart :data="chartData"></column-chart>
      </b-col>
    </b-row>
    <b-row align-h="center" class="mt-5 mb-5">
      <b-col cols="5" class="mr-5" align="center">
        <bar-chart :data="chartData"></bar-chart>
      </b-col>
      <b-col cols="5" align="center">
        <area-chart :data="chartData"></area-chart>
      </b-col>
    </b-row>
    <b-row align-h="center" class="mt-5 mb-5">
      <b-col cols="5" class="mr-5" align="center">
        <line-chart :data="lineData"></line-chart>
      </b-col>
      <b-col cols="5" align="center">
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
const first = [['Ene', 4], ['Feb', 2], ['Mar', 10], ['abr', 5], ['May', 3], ['Jun', 8]]
const second = [['Jul', 13], ['Ago', 5], ['Sep', 9], ['Oct', 6], ['Nov', 8], ['Dic', 15]]
export default {
  data () {
    return {
      selected: 1,
      options: [
        {value: 1, text: '1er Semestre'},
        {value: 2, text: '2do Semestre'}
      ],
      chartData: first,
      lineData: [
        {name: 'Tipo 1', data: {'2018-05-20': 3, '2018-05-21': 4, '2018-05-22': 7, '2018-05-23': 10}},
        {name: 'Tipo 2', data: {'2018-05-20': 5, '2018-05-21': 1, '2018-05-22': 8, '2018-05-23': 7}},
        {name: 'Tipo 3', data: {'2018-05-20': 1, '2018-05-21': 2, '2018-05-22': 3, '2018-05-23': 8}},
        {name: 'Tipo 4', data: {'2018-05-20': 7, '2018-05-21': 6, '2018-05-22': 5, '2018-05-23': 8}}
      ]
    }
  },
  methods: {
    onChange (sel) {
      this.$nextTick(() => {
        this.selected === 1 ? this.chartData = first : this.chartData = second
      })
    }
  }
}
</script>



