<template>
  <b-modal @ok="handleOk" centered size="lg"
          ref="modalGroup"
          id="modalGroup"
          :title="id !== 0 ? 'Editar grupo de contactos' : 'Nuevo grupo de contactos'"
          no-close-on-backdrop
          no-close-on-esc
          lazy
          hide-footer
          :header-bg-variant="headerBgVariant"
          :header-text-variant="headerTextVariant"
          @show="getData"
          @hidden="reset">
    <b-form @submit.stop.prevent="handleSubmit">
      <b-row>
        <b-col>
          <b-form-group id="lbname"
                        label="Nombre del grupo"
                        label-for="txname">
            <b-form-input
                      placeholder="Introduzca el nombre de grupo..."
                      id="txname"
                      v-model="name"
                      required>
            </b-form-input>
          </b-form-group>
        </b-col>
      </b-row>

      <b-row>
        <b-col>
          <b-form-group id="lbdescription"
                        label="Descripción:"
                        label-for="txdescription">
          <b-form-textarea id="txdescription"
                     v-model="description"
                     placeholder="Introduzca la descripcion del grupo..."
                     :rows="2"
                     :max-rows="2"
                     :no-resize="true"
                     required maxlength="400">
          </b-form-textarea>
          </b-form-group>
          <h6 class="label-count">{{ description.length + '/400'}}</h6>
        </b-col>
      </b-row>

      <b-row>
        <b-col cols="5" align-h="center">
          <i class="fas fa-users fa-lg"></i><label class="ml-2">Miembros</label>
        </b-col>
      </b-row>
      <b-row align-h="end">
        <b-col class="text-right" cols="3">
          <b-button size="sm" variant="success"><i class="fas fa-plus"></i></b-button>
          <b-button size="sm" variant="danger"><i class="far fa-trash-alt"></i></b-button>
        </b-col>
      </b-row>
      <b-row class="mt-1">
        <b-col>
          <b-table id="TBContactsInd" hover show-empty head-variant="inverse"
            empty-text="No hay registros para mostrar"
            empty-filtered-text="No hay registros que coincidan con su solicitud"
            stacked="md"
            v-model="tableValues"
            :busy="isBusy"
            :items="myProvider"
            :fields="fields"
            :filter="filter"
            :current-page="currentPage"
            :per-page="perPage"
            @head-clicked="clearSelected">

            <template slot="HEAD_actions" slot-scope="head">
              <input type="checkbox" @click.stop="toggleSelected" v-model="allSelected">
            </template>
            <template slot="actions" slot-scope="item">
              <input class="checkboxtext" type="checkbox" name="checked" :key="item.index" :value="item.item" @click.stop v-model="checkedItems">
            </template>

            <template slot="name" slot-scope="row">{{ row.item.contacts.name + ' ' + row.item.contacts.lastname }}</template>
            <template slot="email" slot-scope="row">{{ row.item.contacts.email }}</template>
          </b-table>

          <b-pagination v-if="!isBusy"
                      size="md"
                      align="center"
                      :total-rows="totalRows"
                      :per-page="perPage"
                      v-model="currentPage">
          </b-pagination>
          <div v-else class="text-center my-2">Cargando Datos...</div>
        </b-col>
      </b-row>

      <b-container>
        <b-row align-h="end">
          <b-button class="mr-2" type="button" v-on:click="onCancel" variant="dark">Cancelar</b-button>
          <b-button class="mr-0" type="submit" variant="primary">Aceptar</b-button>
        </b-row>
      </b-container>
    </b-form>
  </b-modal>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters({
      workgroupId: 'getWorkgroupId',
      id: 'group/getGroupById'
    })
  },
  data () {
    return {
      name: '',
      description: '',
      headerBgVariant: 'success',
      headerTextVariant: 'light',
      isBusy: false,
      currentPage: 1,
      perPage: 10,
      totalRows: 0,
      filter: null,
      checkedItems: [],
      tableValues: [],
      allSelected: false,
      sortField: '',
      fields: [
        {key: 'actions', class: 'text-center'},
        { key: 'name', label: 'Nombre', class: 'text-left' },
        { key: 'email', label: 'Email', class: 'text-left' }
      ]
    }
  },

  watch: {
    checkedItems (a, b) {
      if (this.tableValues.length === a.length) {
        this.allSelected = true
      } else {
        this.allSelected = false
      }
    },
    perPage (a, b) {
      if (a !== b) {
        this.clearSelected()
      }
    },
    currentPage (a, b) {
      if (a !== b) {
        this.clearSelected()
      }
    },
    filter (a, b) {
      if (a !== b) {
        this.clearSelected()
      }
    },
    async (a, b) {
      if (a !== b) {
        this.clearSelected()
      }
    }
  },

  methods: {
    myProvider (ctx) {
      this.isBusy = true
      this.allSelected = ''

      let promise = this.$store.dispatch('group/getListMembers', {
        currentPage: ctx.currentPage,
        sortBy: ctx.sortBy,
        sortDesc: ctx.sortDesc,
        filter: ctx.filter
      })

      return promise.then((response) => {
        const items = response.body.items
        this.totalRows = parseInt(response.body.total)
        this.isBusy = false
        return (items)
      }).catch(response => {
        this.isBusy = false
        return []
      })
    },
    toggleSelected () {
      this.allSelected = !this.allSelected
      this.checkedItems = this.allSelected ? this.tableValues : []
    },
    clearSelected () {
      this.allSelected = false
      this.checkedItems = []
    },

    handleOk (evt) {
      evt.preventDefault()
    },
    getData () {
      if (this.id !== 0) {
        const group = this.$store.state.group.groupItem
        this.name = group.name
        this.description = group.description ? group.description : ''
      }
    },
    reset () {
      this.name = ''
      this.description = ''
      this.currentPage = 1
      this.$store.commit('group/setGroupId', 0)
    },
    handleSubmit () {
      /*
      if (this.recipients.length > 0) {
        this.$store.dispatch('setLoading', true)
        const JsonData = this.formPrepare()
        let promise = this.$store.dispatch('messages/setConversation', JsonData)

        promise.then(data => {
          this.$root.$emit('bv::refresh::table', 'TBConversation')
          this.$root.$emit('bv::hide::modal', 'modalConversation')
          this.$notify({ type: 'success', title: 'Operación <b>exitósa</b>', text: 'Registro agregado exitosamente' })
          this.$store.dispatch('setLoading', false)
        }).catch(() => { this.$store.dispatch('setLoading', false) })
      } else {
        this.$notify({ type: 'warn', title: 'Datos incompletos', text: 'Debe ingresar al menos un (1) destinatario!' })
      }
      */
    },
    onCancel () {
      this.$root.$emit('bv::hide::modal', 'modalGroup')
    }
  }
}
</script>



