<template>
  <b-container fluid>
    <b-row align-v="center" class="ml-1 mt-4 mb-4">
      <h4 class="text-success">Grupos de Contactos</h4>
      <div class="v-divider"></div>
      <b-col>
        <router-link :to="{name: 'dashboard'}"><i class="fas fa-home text-muted mr-2 link--top"></i></router-link>-
        <a class="ml-2">Grupos de Contactos</a>
      </b-col>
    </b-row>
    <b-row>
      <b-col md="6" class="my-1">
        <b-form-fieldset label-text-align="left" horizontal label="Filtrar: " :label-cols="1">
          <b-input-group>
            <b-form-input v-model="filter" placeholder="Buscar por..." />
              <b-input-group-append>
                <b-btn :disabled="!filter" @click="filter = ''">Limpiar</b-btn>
              </b-input-group-append>
          </b-input-group>
        </b-form-fieldset>
      </b-col>
      <b-col align-self="center" class="text-right mb-3">
        <b-button v-b-popover.hover="'Eliminar grupo(s)!'"  class="round-button" v-if="checkedItems.length > 0" @click="this.$refs.deleteRecord.show" variant="danger"><i class="far fa-trash-alt fa-lg"></i></b-button>
        <b-button v-b-popover.hover="'Nuevo grupo!'"  class="round-button" @click="newRecord" variant="primary"><i class="far fa-list-alt fa-lg"></i></b-button>
      </b-col>
    </b-row>

    <b-table id="TBGroup" hover show-empty head-variant="inverse"
            empty-text="No hay registros para mostrar"
            empty-filtered-text="No hay registros que coincidan con su solicitud"
            stacked="md"
            v-model="tableValues"
            :busy="isBusy"
            :items="myProvider"
            :fields="fields"
            :filter="filter"
            :current-page="currentPage"
            :per-page="perPage"
            @head-clicked="clearSelected"
            @row-clicked="editRecord">
      <template slot="HEAD_actions" slot-scope="head">
        <input type="checkbox" @click.stop="toggleSelected" v-model="allSelected">
      </template>
      <template slot="actions" slot-scope="item">
        <input class="checkboxtext" type="checkbox" name="checked" :key="item.index" :value="item.item" @click.stop v-model="checkedItems">
      </template>

      <template slot="name" slot-scope="item">{{ item.value }}</template>
      <template slot="description" slot-scope="item">{{ item.value }}</template>
      <template slot="members" slot-scope="item">{{ item.value }}</template>
    </b-table>

    <b-pagination v-if="!isBusy"
                size="md"
                 align="center"
                 :total-rows="totalRows"
                 :per-page="perPage"
                 v-model="currentPage">
    </b-pagination>
    <div v-else class="text-center my-2">Cargando Datos...</div>

    <modal-group></modal-group>

    <b-modal centered ref="deleteRecord" hide-footer title="Eliminar Registro(s)">
      <div class="d-block text-center mt-1 mb-4">
        <h5>¿Desea eliminar los registros seleccionados?</h5>
      </div>
      <b-row align-h="end">
        <b-button size="sm" class="mr-2 w-25" variant="secondary" @click="onHide">Cancelar</b-button>
        <b-button size="sm" class="mr-2 w-25" variant="primary" @click="deleteRecord">Ok</b-button>
      </b-row>
    </b-modal>

  </b-container>
</template>

<script>
import modalGroup from './editGroup'

export default {
  components: {
    'modal-group': modalGroup
  },
  data () {
    return {
      items: [],
      isBusy: false,
      currentPage: 1,
      perPage: 10,
      totalRows: 0,
      filter: null,
      checkedItems: [],
      tableValues: [],
      allSelected: false,
      fields: [
        { key: 'actions', class: 'text-center' },
        { key: 'name', label: 'Nombre', sortable: true, class: 'text-left' },
        { key: 'description', label: 'Descripción', sortable: true, class: 'text-left' },
        { key: 'members', label: 'Miembros', sortable: true, class: 'text-center' }
      ]
    }
  },
  watch: {
    checkedItems (a, b) {
      if (this.tableValues.length === a.length) {
        this.allSelected = true
      } else {
        this.allSelected = false
      }
    },
    perPage (a, b) {
      if (a !== b) {
        this.clearSelected()
      }
    },
    currentPage (a, b) {
      if (a !== b) {
        this.clearSelected()
      }
    },
    filter (a, b) {
      if (a !== b) {
        this.clearSelected()
      }
    },
    async (a, b) {
      if (a !== b) {
        this.clearSelected()
      }
    }
  },
  methods: {
    myProvider (ctx) {
      this.isBusy = true
      this.allSelected = ''

      let promise = this.$store.dispatch('group/getListGroup', {
        currentPage: ctx.currentPage,
        sortBy: ctx.sortBy,
        sortDesc: ctx.sortDesc,
        filter: ctx.filter
      })

      return promise.then((response) => {
        const items = response.body.items
        this.totalRows = parseInt(response.body.total)
        this.isBusy = false
        return (items)
      }).catch(response => {
        this.isBusy = false
        return []
      })
    },

    toggleSelected () {
      this.allSelected = !this.allSelected
      this.checkedItems = this.allSelected ? this.tableValues : []
    },
    clearSelected () {
      this.allSelected = false
      this.checkedItems = []
    },
    editRecord (record, index, e) {
      this.$store.dispatch('setLoading', true)
      this.$store.dispatch('group/setGroupId', record.id).then(() => {
        this.$root.$emit('bv::show::modal', 'modalGroup')
        this.$store.dispatch('setLoading', false)
      }).catch(() => { this.$store.dispatch('setLoading', false) })
    },
    newRecord () {
      this.$root.$emit('bv::show::modal', 'modalGroup')
    },
    onHide () {
      this.$refs.deleteRecord.hide()
    },
    refresh () {
      this.clearSelected()
      this.$root.$emit('bv::refresh::table', 'TBGroup')
    },
    deleteRecord () {
      this.$store.dispatch('setLoading', true)
      this.$store.dispatch('group/deleteGroup', this.checkedItems).then((response) => {
        if (response.body.message === 'Data in relation group_member, block delete') {
          this.$notify({ type: 'warn', title: 'Operación <b>no exitósa</b>', text: 'El grupo posee miembros asociados!' })
        } else {
          this.$notify({ type: 'success', title: 'Operación <b>exitósa</b>', text: 'Registro(s) eliminado(s) exitosamente!' })
        }
        this.refresh()
        this.onHide()
        this.$store.dispatch('setLoading', false)
      }).catch(data => {
        this.$store.dispatch('setLoading', false)
        this.$notify({ type: 'error', title: 'Error del sistema', text: 'Error: ' + data.body.message })
      })
    }
  }
}
</script>



