import Vue from 'vue'
import VueResource from 'vue-resource'
import store from '../vuex/index'
Vue.use(VueResource)

const baseUrl = require('./config.js').ConectionUrlBase

const commonManager = {
  /**
   *  Obtiene el listado de todos los grupos de contactos
   *  @param {string} url
   *  @return {object} response
   */
  getListRecord (url) {
    /** Devuelve una promesa con el resultado de la consulta a la bd */
    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + url).then(response => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  getRecordById (url) {
    return new Promise((resolve, reject) => {
      Vue.http.get(baseUrl + url).then((response) => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  insertRecord (url, jsonData) {
    return new Promise((resolve, reject) => {
      Vue.http.post(baseUrl + url, jsonData).then((response) => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  updateRecord (url, jsonData) {
    return new Promise((resolve, reject) => {
      Vue.http.put(baseUrl + url, jsonData).then((response) => {
        resolve(response)
      }).catch(response => {
        store.dispatch('globalErrorHandler', response)
        reject(response)
      })
    })
  },

  deleteRecord (url, record) {
    return new Promise((resolve, reject) => {
      for (var i = 0; i < record.length; i++) {
        Vue.http.delete(baseUrl + url + record[i].id).then(response => {
          resolve(response)
        }).catch(response => {
          store.dispatch('globalErrorHandler', response)
          reject(response)
        })
      }
    })
  }
}

export default commonManager



